<template>
  <div id="home">
    <div class="container">
      <div class="header">
        <img src="@/assets/logo.png" alt="">
      </div>
      <div class="content">
        <van-tabs>
          <van-tab v-for="(item,index) in tabs" :key="index" :title="item.name">
            <bannercom></bannercom>
            <childcom :list='item.childList'></childcom>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import bannercom from '../components/banner'
import childcom from '../components/child'
export default {
  data() {
    return {
      tabs:[
        {
          name:'首页'
        },
        {
          name:'关于大象',
          childList:[
            {
              name:'公司简介',
            },
            {
              name:'公司荣誉',
            },
            {
              name:'公司公益',
            },
            {
              name:'招贤纳士',
            },
            {
              name:'联系我们',
            },
          ]
        },
        {
          name:'公司产品',
          childList:[
            {
              name:'产品视频介绍',
            },
            {
              name:'产品介绍',
            },
            {
              name:'产品演示',
            },
          ]
        },
        {
          name:'核心技术',
          childList:[
            {
              name:'专利认证',
            },
            {
              name:'核心技术1',
            },
            {
              name:'核心技术2',
            },
          ]
        },
        {
          name:'项目案例',
          childList:[
            {
              name:'2013-2018年项目部分典型案例',
            },
            {
              name:'2019项目展示',
            },
          ]
        },
        {
          name:'新闻资讯',
          childList:[
            {
              name:'公司新闻',
            },
            {
              name:'行业新闻',
            },
          ]
        },
        {
          name:'联系我们'
        },
      ]
    };
  },
  components:{
    bannercom,
    childcom
  },
  mounted(){

  }
};
</script>

<style lang="less">
#home{
  .container{
    .header{
      height: 60px;
      img{
        height: 50px;
        padding: 5px 0 0 5px;
      }
    }
  }
}
</style>


